@mixin retina {
  @media (min-resolution: $retina-dpi),
    (min-resolution: $retina-dppx) {
    @content;
  }
}

// Desktop first

@mixin vp-1919 {
  @media (max-width: $vp-1920 - 1) {
    @content;
  }
}

@mixin vp-1839 {
  @media (max-width: $vp-1840 - 1) {
    @content;
  }
}

@mixin vp-1439 {
  @media (max-width: $vp-1440 - 1) {
    @content;
  }
}

@mixin vp-1279 {
  @media (max-width: $vp-1280 - 1) {
    @content;
  }
}

@mixin vp-1023 {
  @media (max-width: $vp-1024 - 1) {
    @content;
  }
}

@mixin vp-767 {
  @media (max-width: $vp-768 - 1) {
    @content;
  }
}

@mixin vp-374 {
  @media (max-width: $vp-375 - 1) {
    @content;
  }
}

// ---------------------------------

// не нужно добавлять свои миксины для ресета списков, обнуления кнопок и прочие
// этим вы усложняете работу команды, проверку вашего кода и будущую поддержку проекта

@mixin ptmono($size, $height) {
  font-family: "PT Mono Regular", "Courier New", "Courier", monospace;
  font-style: normal;
  font-size: $size;
  line-height: $height;
  font-weight: 400;
}

@mixin roboto($size, $height) {
  font-family: "Roboto Condensed Regular", "Arial", "Helvetica", sans-serif;
  font-style: normal;
  font-size: $size;
  line-height: $height;
  font-weight: 400;
}

@mixin roboto-bold($size, $height) {
  font-family: "Roboto Condensed Bold", "Arial", "Helvetica", sans-serif;
  font-style: normal;
  font-size: $size;
  line-height: $height;
  font-weight: 700;
}
